        title: 'FourthWall Creative (Present)',
        description:
          'Maintained and developed software used by Premier League Football. Dealing with hundreds of thousands of records. <br><br> Created a Laravel API print server for automated sending and printing of data. <br><br>Developing new COSS (Customer Own System Solution) integrating into Royal Mail to create a custom mailing system.',
