  methods: {
    // jspdf does not include the bootstrap style layout
    generatePdf() {
      // const img = new Image();
      const imgData = 'data:image/png;base64,';
      // console.log(imgData);
      const doc = new Jspdf('p', 'pt', 'A4');
      doc.addImage(imgData, 'PNG', 270, 750, 65, 65);
      this.margins = {
        top: 80,
        bottom: 60,
        left: 40,
        width: 522,
      };
      doc.setFontSize(30);
      doc.setFont('helvetica', 'bold');
      doc.text('John Murphy', 300, 60, 'center');
      doc.setFontSize(9);
      doc.text('Fourth Wall Creative April 2019 - Present', 50, 240);
      doc.text('The Twelth Man June 2015 - 2019', 50, 350);
      doc.text('Shortlands Tavern September 2010 - 2015', 50, 390);
      doc.text('Employment History', 300, 205, 'center');
      doc.text('Personal Development', 300, 450, 'center');
      doc.text('Electagon - Election Prediction Tool - Project ongoing', 50, 480);
      doc.text('Education', 300, 560, 'center');
      doc.text('Aberystwyth University 2007 - 2010', 50, 580);
      doc.text('Pensby High/College 1998 - 2007', 50, 620);
      doc.text('About Me', 300, 670, 'center');
      // doc.text('Projects', 300, 280, 'center');
      // doc.text('Employment History', 50, 195);
      doc.text('Email: johncraigmurphy@gmail.com - Telephone: 07908142588', 300, 100, 'center');
      doc.textWithLink('Portfolio: https://soladragon.github.io/johncraigmurphy/', 185, 130, { url: 'https://soladragon.github.io/johncraigmurphy/' });
      doc.setFontSize(8);
      doc.text('Curriculum Vitae', 3, 10);
      doc.text('$skillSet = [[PHP, JAVASCRIPT, MYSQL, HTML5, CSS3], [LARAVEL, CODEIGNITER, VUE, JQUERY], [BOOTSTRAP, GIT, DOCKER]]', 296, 160, 'center');
      doc.textWithLink('https://www.linkedin.com/in/johncraigmurphy/', 210, 835, { url: 'https://www.linkedin.com/in/johncraigmurphy/' });
      // doc.line(296, 300, 296, 800); // vertical line
      doc.line(50, 170, 545, 170); // horizontal line
      doc.text(60, 260, doc.splitTextToSize('- Developed systems used by Premier League Football Clubs. Direct control of over a million membership records.', 495));
      doc.text(60, 275, doc.splitTextToSize('- Developed a Laravel/CUPS print server for automated sending and printing of files through an API.', 495));
      doc.text(60, 290, doc.splitTextToSize('- Integrating into Royal Mail with a customer own system solution. A mailing system used to integrate with our Premier League system and other upcoming projects.', 495));
      doc.text(60, 315, doc.splitTextToSize('- Integrating, scheduling and consuming large amounts of data for the Premier League Football Clubs with the largest event ticket search engine and gate access security provider.', 495));
      doc.text(60, 365, doc.splitTextToSize('- Day to day running of the pub along with sorting timetables, counting stock, balancing safe, banking and managing staff.', 495));
      doc.text(60, 405, doc.splitTextToSize('- After graduating university I worked as a manager of this wonderful pub. This taught me a great deal in customer service and dealing with a wide range of people.', 495));
      doc.text(60, 495, doc.splitTextToSize('Using the http://www.data.parliament.uk/dataset/elections API I proceeded to consume, filter and organise the data using a Laravel back end on my localhost to export a json file. I then built a front end in Vue to manipulate the JSON file data and display the results of the UK general election based on numerous variables input by the user.', 495));
      doc.textWithLink('https://soladragon.github.io/electagon/', 60, 530, { url: 'https://soladragon.github.io/electagon/' });
      doc.text(60, 595, doc.splitTextToSize('Bachelor of Science with Honours - Business Information Technology', 495));
      doc.text(60, 635, doc.splitTextToSize('Grades A-C in Maths, English, Science, IT', 495));
      doc.text(60, 700, doc.splitTextToSize('I usually try to start my day by reading the news as I like to be up to date with current events. I enjoy learning and increasing my knowledge and I am currently working through courses regarding Linux, Apache, AWS, React and Docker in more detail. Something I am excited to learn in the future is more C# and the .NET platform.', 495));
      doc.text(60, 730, doc.splitTextToSize('I like cycling, playing online games (Guild Wars 2, Final Fantasy Online), grow vegetables (particularly tomatoes).', 495));
      doc.output('dataurlnewwindow');

      doc.save('John-Murphy-CV.pdf');
    },
  },
  async mounted() {
    this.loaded = false;
    this.loaded2 = false;
    axios.get('https://teamtreehouse.com/johncraigmurphy.json')
      .then((response) => {
      // JSON responses are automatically parsed.
        this.chartdata2 = response.data;
        // alert(JSON.stringify(this.chartdata2.points.total));
        Object.keys(this.chartdata2.points).forEach((k) => {
          // alert(this.chartdata2.k);
          if (this.chartdata2.points[k] > 50 && this.chartdata2.points[k] < 8000) {
            this.chartdata.datasets[0].data.push(this.chartdata2.points[k]);
            this.chartdata.labels.push(k);
          }
          // console.log(`${k} - ${this.chartdata2.points[k]}`);
        });
        this.loaded = true;
      })
      .catch((e) => {
        this.errors.push(e);
      });

    axios.get('https://api.github.com/users/soladragon/repos?sort=created')
      .then((response2) => {
      // JSON responses are automatically parsed.
        // this.githubInfo = response2.data;
        // alert(JSON.stringify(this.githubInfo));
        Object.keys(response2.data).forEach((k) => {
          // alert(JSON.stringify(response2.data[k].fork));
          if (response2.data[k].fork === false && response2.data[k].has_pages === true) {
            this.githubInfo.push(response2.data[k]);
          }
        });
        this.loaded2 = true;
      })
      .catch((e) => {
        this.errors.push(e);
      });
  },
